import React, { useCallback } from 'react';
import { SupportHistoryListType } from '../../../../type/support-history/support-history.type';
import { RectLabel } from '../../label/rect-label/rect-label';
import './support-history-card.scss';
import { DateFormatter } from '../../../../utilities/date-formatter';

type Props = {
  onClick: (id:number) => void,
  supportHistoryData: SupportHistoryListType,
  className?: string,
  isSP?: boolean,
}

export const SupportHistoryCard = (props: Props) => {
  const {
    supportHistoryData, className, onClick, isSP,
  } = props;

  const baseClassName = 'support_history_card';
  const defaultClassName = isSP ? `${baseClassName}_sp` : `${baseClassName}_pc`;

  /* callback */
  const handleClickSupportHistoryDetail = useCallback(
    () => {
      if (onClick) {
        onClick(supportHistoryData?.id);
      }
    }, [onClick],
  );

  return (
    <div
      className={`${defaultClassName} card_base ${className}`}
      onClick={handleClickSupportHistoryDetail}
    >
      <div className="row1 card_base_row">
        <div className="row1_col1 card_base_col">
          <div>{`${supportHistoryData.customer_name}`}</div>
          <div className="important">{`${supportHistoryData.project_name || '---'}`}</div>
          <div>{`${supportHistoryData.supported_history_name || '---'}`}</div>
          <div>{supportHistoryData.supported_person || '---'}</div>
        </div>
        <div className="row1_col2">
          <div className="date">{DateFormatter.date2str(supportHistoryData.reception_time) || '---'}</div>
          <div className="thumb">thumb</div>
          <div className={`${!supportHistoryData.fixed_flag ? 'invisible' : ''}`}>
            <RectLabel label="" />
          </div>
        </div>
      </div>
    </div>
  );
};

SupportHistoryCard.defaultProps = { isSP: false };
