import {
  useCallback, useEffect, useMemo, useState,
} from 'react';
import { DropdownProps, Select as SSelect, SelectProps } from 'semantic-ui-react';
import { Validation } from '../../../model/validation/validation';
import { ValidationNotEmpty } from '../../../model/validation/validation-not-empty';
import { UserAgent } from '../../../utilities/user-agent';
import { ErrorPop } from '../error-pop/error-pop';
import { Required } from '../required/required';
import './select.scss';

type Props = {
  validationList?: Validation[];
  /**   */
  touch?: boolean;
  noRequireLabel?:boolean;
  require?: boolean;
  defaultLabel?: string;
  onChange: (v: string | number) => void;
  id?: string;
  label?: string;
  value?: number | string;
  /** string Valuetrue */
  title?: string | boolean;
  options: { text: string, value: string | number }[]
} & Omit<SelectProps, 'onChange' | 'options'>

export const Select = (props: Props) => {
  const {
    validationList,
    require,
    touch,
    value,
    disabled,
    options,
    onChange,
    // eslint-disable-next-line
    defaultLabel,
    className,
    placeholder,
    id,
    title,
    label,
    noRequireLabel,
  } = props;

  /* State */
  const [focus, setFocus] = useState<boolean>(false);
  const [errorList, setErrorList] = useState<string[]>([]);
  const [changed, setChanged] = useState<boolean>(false);
  const error = Boolean((touch || changed) && errorList.length);

  /* Memo */
  const tip = useMemo(() => {
    if (!title) return undefined;
    if (typeof title === 'string') {
      return title;
    }
    if (Number.isNaN(value)) {
      return options[0].text;
    }
    return title ? options.find((v) => v.value === value)?.text || '' : '';
  }, [title, value, options]);

  /* Callback */
  // eslint-disable-next-line
  const handleOnChangeSP = useCallback((v: string | number) => {
    setChanged(true);
    if (onChange) {
      onChange(Number(v));
    }
  },
  [changed, onChange]);

  const handleOnChange = useCallback(
    (event: globalThis.React.SyntheticEvent<HTMLElement>, data: DropdownProps) => {
      setChanged(true);
      if (onChange) {
        onChange(data.value as string | number);
      }
    },
    [changed, onChange],
  );

  // eslint-disable-next-line
  const handleOnBlurSP = useCallback(
    (bv: number) => {
      setChanged(true);
      setFocus(false);
      if (!validationList) {
        setErrorList(
          require ? ValidationNotEmpty
            .filter((v) => !v.run(bv ? String(bv) : ''))
            .map((v) => v.errorMessage)
            : [],
        );
      } else {
        setErrorList(
          require ? [...ValidationNotEmpty, ...validationList]
            .filter((v) => !v.run(bv ? String(bv) : ''))
            .map((v) => v.errorMessage)
            : validationList
              .filter((v) => !v || !v.run(bv ? String(bv) : ''))
              .map((v) => v.errorMessage),
        );
      }
    },
    [],
  );
  const handleOnBlur = useCallback(
    (_:any, data: DropdownProps) => {
      setChanged(true);
      setFocus(false);
      if (!validationList) {
        setErrorList(
          require ? ValidationNotEmpty
            .filter((v) => !v.run(data.value ? String(data.value) : ''))
            .map((v) => v.errorMessage)
            : [],
        );
      } else {
        setErrorList(
          require ? [...ValidationNotEmpty, ...validationList]
            .filter((v) => !v.run(data.value ? String(data.value) : ''))
            .map((v) => v.errorMessage)
            : validationList
              .filter((v) => !v || !v.run(data.value ? String(data.value) : ''))
              .map((v) => v.errorMessage),
        );
      }
    },
    [],
  );

  useEffect(() => {
    if (touch) {
      if (!validationList) {
        setErrorList(
          require ? ValidationNotEmpty
            .filter((v) => !v.run(value ? String(value) : ''))
            .map((v) => v.errorMessage)
            : [],
        );
      } else {
        setErrorList(
          require ? [...ValidationNotEmpty, ...validationList]
            .filter((v) => !v.run(value ? String(value) : ''))
            .map((v) => v.errorMessage)
            : validationList
              .filter((v) => !v.run(value ? String(value) : ''))
              .map((v) => v.errorMessage),
        );
      }
    }
  }, [touch]);

  return (
    <div
      className={`pulldown_select ${className || ''}`}
      title={tip}
      onFocus={() => setFocus(true)}
    >
      <label className={`for_select ${UserAgent === 'pc' ? ' error_focus' : ''}`}>
        <span>{label}</span>{label && require && !noRequireLabel && <Required />}
        {UserAgent === 'sp'
          && (
          <div className="selector">
            <select
              className={`${error ? 'error' : ''}`}
              id={id}
              disabled={disabled}
              onBlur={(e) => { handleOnBlurSP(Number(e.target.value) ?? NaN); }}
              onChange={(e) => { handleOnChangeSP(Number(e.target.value) ?? NaN); }}
              value={value ?? NaN}
            >
              {defaultLabel && <option key="aaaa" value={String(NaN)}>{defaultLabel}</option>}
              {options.map((v, i) => (
                <option key={`${v.text}${i}`} value={String(v.value)}>{v.text}</option>
              ))}
            </select>
          </div>
          )}
        {UserAgent === 'pc'
        && (
        <SSelect
          id={id}
          error={error}
          value={value ?? NaN}
          disabled={disabled}
          onChange={handleOnChange}
          onBlur={handleOnBlur}
          placeholder={placeholder}
          options={defaultLabel ? [
            { text: defaultLabel, value: NaN },
            ...options,
          ] : options}
        />
        )}
      </label>
      {error && (UserAgent === 'sp' ? focus : true) && (
        <ErrorPop messages={errorList} />
      )}
    </div>
  );
};
