import { useCallback, useMemo, useState } from 'react';
import { useDispatch } from 'react-redux';
import { Button } from '../../ui/button/button';
import { DialogActions } from '../../../redux/dialog/dialog.action';
import { EstimateActions } from '../../../redux/estimate/estimate.action';
import { Estimate, EstimateListType } from '../../../type/estimate/estimate.type';
import { SVGTemplateOmitsumorisho } from '../../../asset/svg-template/mitshumori/svg-template-omitsumorisho';
import { SvgTemplateKojiMeisaisho } from '../../../asset/svg-template/seikyu/svg-template-koji-meisaisho';

export type PrintType =
  'estimate'
  | 'estimateIn'
  | 'claim'
  | 'claimIn'

type Props = {
  data: any;
  type: PrintType;
  estimate?: Estimate;
  estimateListData?: EstimateListType;
}

export const PrintPreview = (props: Props) => {
  const {
    data, type, estimate, estimateListData,
  } = props;
  window.console.log(data);

  /* Hooke */
  const dispatch = useDispatch();

  /* State */
  const [svgState, setSvgState] = useState('');

  /* Callback */
  const encode = (svg: SVGSVGElement) => {
    setSvgState(btoa(unescape(encodeURIComponent(svg.outerHTML))));
  };

  const handleClickPrint = useCallback(
    () => {
      window.console.log(svgState);
      dispatch(EstimateActions.api.print({
        id: 1,
        data: {
          files: [],
        },
      }));
      dispatch(DialogActions.pop());
    },
    [setSvgState],
  );

  /* Memo */
  const template = useMemo(() => {
    switch (type) {
      case 'estimate':
        return (
          <SVGTemplateOmitsumorisho
            estimate={estimate}
            estimateListData={estimateListData}
            callback={encode}
          />
        );
      case 'estimateIn':
        return <SvgTemplateKojiMeisaisho callback={encode} />;
      case 'claim':
        return <SvgTemplateKojiMeisaisho callback={encode} />;
      case 'claimIn':
        return <SvgTemplateKojiMeisaisho callback={encode} />;
      default:
        return undefined;
    }
  }, [type]);
  return (
    <div className="PrintPreview">
      <div className="preview_area">
        {template}
      </div>
      <div className="base_footer">
        <div>
          
          <span style={{ color: 'red' }}>  </span>
          
          <span style={{ color: 'red' }}>  </span>
          
        </div>
        <div style={{ display: 'flex' }}>
          <div>
            <Button size="md" color="primary" onClick={handleClickPrint}>
              
            </Button>
            <Button size="md" color="dark" onClick={() => dispatch(DialogActions.pop())}>
              
            </Button>
          </div>
          <div>
            <Button size="md" color="primary" onClick={() => dispatch(DialogActions.pushReady())}>
              
            </Button>
            <Button size="md" color="primary" onClick={() => dispatch(DialogActions.pushReady())}>
              
            </Button>
          </div>
        </div>
      </div>
    </div>
  );
};
