import cloneDeep from 'lodash/cloneDeep';
import { useMemo } from 'react';
import { EstimateListType } from '../../../../type/estimate/estimate.type';
import { MathHelper } from '../../../../utilities/math-helper';
import { UserAgent } from '../../../../utilities/user-agent';
import './estimate-card.scss';
import { DateFormatter } from '../../../../utilities/date-formatter';

type Props = {
  data: EstimateListType,
  className?: string,
  callback:(data:EstimateListType)=>void,
}

export const EstimateCard = (props: Props) => {
  const { data, className, callback } = props;

  const defaultClassName = useMemo(
    () => (UserAgent === 'sp' ? 'estimate_card_sp' : 'estimate_card_pc'),
    [UserAgent],
  );

  return (
    <div
      className={`${defaultClassName} card_base ${className}`}
      onClick={() => callback(cloneDeep(data))}
    >
      <div className="row1 card_base_row">
        <div className="row1__col1"></div>
        <div className="row1__col2"></div>
        <div className="row1__col3">{data.customer_name || '---'}{/* {data.furigana}*/}</div>
      </div>
      <div className="row2 card_base_row">
        <div className="row2__col1"></div>
        <div className="row2__col2"></div>
        <div className="row2__col3">{data.field_name || '---'}</div>
      </div>
      <div className="row3 card_base_row">
        <div className="row3__col1"></div>
        <div className="row3__col2"></div>
        <div className="row3__col3">{data.project_name || '---'}</div>
      </div>
      <div className="row4 card_base_row">
        <div className="row4__col1"></div>
        <div className="row4__col2"></div>
        <div className="row4__col3">{data.project_representative_name || '---'}</div>
      </div>
      <div className="row5 card_base_row">
        <div className="row5__col1"></div>
        <div className="row5__col2"></div>
        <div className="row5__col3">{DateFormatter.date2str(data.quote_date) || '---'}</div>
      </div>
      <div className="row6 card_base_row ">
        <div className="row6__col1"></div>
        <div className="row6__col2"></div>
        <div className="row6__col3 important">{data.quote_price ? `${MathHelper.localStr(data.quote_price)}` : '---'}</div>
      </div>
    </div>
  );
};
