import './error-pop.scss';

export type Props = {
  messages: string[];
}

export const ErrorPop = (props:Props) => {
  const { messages } = props;
  return (
    <div className="error_pop">
      {messages.map((v, i) => (
        <div key={`input${i}`} style={{ marginRight: '5px' }}>
          {v}
        </div>
      ))}
    </div>
  );
};
