import { useDispatch, useSelector } from 'react-redux';
import { cloneDeep, isEqual } from 'lodash';
import { useState, useCallback } from 'react';
import { Button } from '../../../../../ui/button/button';
import { DialogActions } from '../../../../../../redux/dialog/dialog.action';
import { DatePicker } from '../../../../../ui/date-picker/date-picker';
import { Select } from '../../../../../ui/select/select';
import { Input } from '../../../../../ui/input/input';
import { SupportHistorySortState } from '../../../../../../type/support-history/support-history.type';
import { useDidMount } from '../../../../../../hooks/life-cycle';
import { CustomerDetailActions } from '../../../../../../redux/customer-detail/customer-detail.action';
import { MasterActions } from '../../../../../../redux/master/master.action';
import { State } from '../../../../../../redux/root.reducer';

type Props = {
  callback: (sort:SupportHistorySortState) => void;
}

export const RefindSupportHistoryTable = (props: Props) => {
  const { callback } = props;
  /* Hook */
  const dispatch = useDispatch();
  const sort = useSelector((state: State) => state.customerDetail.supportSort, isEqual);

  /* State */
  const [sortState, setSortState] = useState(cloneDeep(sort));
  const originList = useSelector((state: State) => state.master.originList, isEqual);

  /* Callback */
  const handleClickSearch = useCallback(() => {
    dispatch(DialogActions.pop());
    callback(sortState);
  }, [callback, sort]);

  const setState = useCallback((v: SupportHistorySortState) => {
    setSortState(cloneDeep(v));
    dispatch(CustomerDetailActions.setSupportSort(v));
  }, [dispatch]);

  /* Master */
  useDidMount(() => {
    dispatch(MasterActions.api.origin.getList({}));
  });
  return (
    <div className="refind_wrap">
      <div className="refind_body">
        <div className="item_wrap">
          <div className="item_box">
            <div className="item_head"></div>
            <Input
              className=""
              value={sortState.project_name}
              onChange={(e) => setState({ ...sortState, project_name: e.target.value })}
            />
          </div>
          <div className="item_box">
            <div className="item_head"></div>
            <Input
              className=""
              value={sortState.project_name}
              onChange={(e) => setState({ ...sortState, project_name: e.target.value })}
            />
          </div>
          <div className="item_box">
            <div className="item_head"></div>
            <DatePicker
              date={null}
              onChange={() => {}}
            />
          </div>
        </div>
        <div className="item_wrap">
          <div className="item_box">
            <div className="item_head"></div>
            <Input
              className=""
              label=""
              value=""
            />
          </div>
        </div>
        <div className="item_wrap">
          <div className="item_box">
            <div className="item_head"></div>
            <DatePicker
              date={null}
              onChange={() => {}}
            />
          </div>
          <div className="item_box">
            <div className="item_head"></div>
            <DatePicker
              date={null}
              onChange={() => {}}
            />
          </div>
          <div className="item_box">
            <div className="item_head"></div>
            <DatePicker
              date={null}
              onChange={() => {}}
            />
          </div>
        </div>
        <div className="item_wrap">
          <div className="item_box">
            <div className="item_head"></div>
            <DatePicker
              date={null}
              onChange={() => {}}
            />
          </div>
          <div className="item_box">
            <div className="item_head"></div>
            <DatePicker
              date={null}
              onChange={() => {}}
            />
          </div>
          <div className="item_box">
            <div className="item_head"></div>
            <DatePicker
              date={null}
              onChange={() => {}}
            />
          </div>
        </div>
        <div className="item_wrap">
          <div className="item_box">
            <div className="item_head"></div>
            <Select
              className=""
              label=""
              value=""
              defaultLabel=""
              onChange={() => {}}
              options={originList.map((v) => ({
                value: v.id, text: v.name,
              }))}
            />
          </div>
          <div className="item_box">
            <div className="item_head"></div>
            <Input
              className=""
              label=""
              value=""
            />
          </div>
        </div>
      </div>
      <footer className="base_footer refind_footer">
        <Button
          size="md"
          color="primary"
          onClick={handleClickSearch}
        >
        </Button>
        <Button
          size="md"
          color="dark"
          onClick={() => dispatch(DialogActions.pop())}
        >
        </Button>
      </footer>
    </div>
  );
};
