import React, { useCallback } from 'react';
import { useDispatch } from 'react-redux';
import { DialogActions } from '../../../../redux/dialog/dialog.action';
import { FileListType } from '../../../../type/file/file.type';
import { Button } from '../../button/button';
import './file-card.scss';
import dummyImg from '../../../../asset/images/show_image_dialog_dummy.jpg';
import { ShowImageDialog } from '../../../dialogs/show-image-dialog/show-image-dialog';

type Props = {
    onClick: (id:number)=>void,
    fileData: FileListType,
    className?: string,
    isSP?: boolean
}

export const FileCard = (props: Props) => {
  const dispatch = useDispatch();
  const ref = React.createRef();
  const {
    onClick, fileData, className, isSP,
  } = props;

  /* Callback */
  const handleClickCustomerDetail = useCallback(
    () => {
      onClick(fileData.id);
    }, [onClick],
  );

  const baseClassName = 'file_card';
  const defaultClassName = isSP ? `${baseClassName}_sp` : `${baseClassName}_pc`;
  // const typeName = fileData.estimated_rank && (fileData.estimated_rank === 1 ? 'OB' : '');

  return (
    <div
      className={`${defaultClassName} card_base ${className || ''}`}
      onClick={(e) => {
        if (e.target === ref.current) {
          return;
        }
        handleClickCustomerDetail();
      }}
    >

      <div className="row1 card_base_row">
        <div className="row1_col1 card_base_col">
          <div>{fileData.no ? `No${fileData.no}` : '---'}</div>
          <div className="important">{`${fileData.file_name || '---'}`}</div>
          <div>
            <div></div>
            <div>{`${fileData.customer_name || '---'}${fileData.furigana ? `${fileData.furigana}` : '---'}`}</div>
          </div>
          <div>
            <div></div>
            <div>{fileData.project_name || '---'}</div>
          </div>
        </div>
        <div className="row1_col2">
          <div className="thumb">
            <img src={fileData.icon_thumbnail} alt="" className="no_shadow" />
          </div>
          <div className={fileData?.format === 'jpg' ? '' : 'invisible'}>
            <Button
              size="sm"
              color="secondary"
              onClick={() => {
                dispatch(DialogActions.push({
                  title: '',
                  element: <ShowImageDialog src={fileData.icon_thumbnail || dummyImg} />,
                }));
              }}
              ref={ref}
            >
            </Button>
          </div>
        </div>
      </div>

      <div className="card_base_row row2">
        <div className="row2_col1">{fileData.upload_date || '---'}</div>
        <div className="row2_col2">
          <div></div>
          <div>{fileData.updater || '---'}</div>
        </div>
      </div>
    </div>
  );
};

FileCard.defaultProps = { isSP: false };
